/*Viewports*/
.page__body {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #333333;
  background-color: #ffffff;
  overflow-x: hidden;
  margin: 0;
}

.page__container {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page__container {
    min-width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__container {
    max-width: 1200px;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}

.main-header {
  position: relative;
  background-color: #ffffff;
}

.main-header__logo {
  position: absolute;
  width: 185px;
  height: 96px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.main-nav {
  position: relative;
  min-height: 56px;
  z-index: 1;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    display: block;
  }
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__list {
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    color: #333333;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (max-width: 767px) {
  .main-nav__list::after {
    position: absolute;
    content: "";
    bottom: 0;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    mix-blend-mode: normal;
  }
}

.main-nav__item {
  background-color: #81b3d2;
  text-align: center;
  width: 100%;
  height: 56px;
  padding: 17px 0 13px;
}
.main-nav__item:hover {
  background-color: #669ec0;
}
.main-nav__item:active, .main-nav__item:focus {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .main-nav__item:not(:first-child) {
    border-top: 1px solid #9ac2db;
  }
  .main-nav__item:not(:last-child) {
    border-bottom: 1px solid #678fa8;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    background-color: transparent;
  }
  .main-nav__item:nth-child(3) {
    grid-column: 4/5;
  }
}

.main-nav__link {
  color: inherit;
}
@media (min-width: 768px) {
  .main-nav__link--current {
    color: #766357;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle::after {
    content: " ";
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
  }
  .main-nav--closed .main-nav__toggle:hover::after {
    background-color: rgba(129, 179, 210, 0.6);
    box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.6), 0 16px 0 0 rgba(129, 179, 210, 0.6);
  }
  .main-nav--closed .main-nav__toggle:active::after {
    background-color: rgba(129, 179, 210, 0.3);
    box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.3), 0 16px 0 0 rgba(129, 179, 210, 0.3);
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle {
    z-index: 2;
  }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    left: 0;
    width: 28px;
    height: 3px;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle:hover::before, .main-nav--opened .main-nav__toggle:hover::after {
    background-color: rgba(255, 255, 255, 0.6);
  }
  .main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs {
  position: static;
}

.main-nav--nojs .main-nav__link {
  display: block;
}

