.map {
  position: relative;
}
.map::before {
  position: absolute;
  content: "";
  width: 27px;
  height: 27px;
  background-color: #ffffff;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 50%;
}
.map::after {
  position: absolute;
  content: "";
  width: 17px;
  height: 17px;
  background-color: #81b3d2;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 50%;
}